@use 'base/variables' as *;
@use 'base/mixins' as *;

.filtros {
    background-color: darken($blanco, 10%);
    margin-top: 2rem;
    border-radius: .3rem;
    padding: 2rem 1rem;
    text-align: center;

    @include telefono {
        padding: 2rem;
    }

    .filtros-inputs {
        @include tablet {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 1rem;
        }
    }

    h2 {
        color: $indigo;
        font-size: 2.5rem;

    }

    .campo {
        width: 100%;
        margin-bottom: 1rem;

        @include telefono {
            margin-bottom: 0;
        }

        label {
            padding: .5rem 2rem;
            cursor: pointer;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 1rem;

            @include tablet {
                justify-content: center;
            }
            
            &:hover {
                background-color: $indigo;
                transition-property: all;
                transition-duration: .2s;
                border-radius: 5rem;
                padding: .5rem 2rem;
                color: $blanco;
            }
        }
    }

    input[type="radio"] {
        accent-color: $indigo;
        width: 1.6rem;
        height: 1.6rem;
        display: none;
    }

    input[type="radio"]:checked~label {
        background-color: $indigo;
        transition-property: all;
        transition-duration: .2s;
        border-radius: 5rem;
        padding: .5rem 2rem;
        color: $blanco;
    }

    input[type="radio"]:disabled~label{
        background-color: transparent !important;
        color: $negro
    }
}

.todas,
.pendientes,
.completas {
    display: flex;
    justify-content: center;
    align-items: center;

    span {
        display: inline-block;
        color: $blanco;
        border-radius: 50%;
        width: 2.5rem;
        height: 2.5rem;
        font-weight: 600;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.pendientes span {
    background-color: lighten($naranja, 20%);
}

.completas span {
    background-color: lighten($verde, 5%);
}

.todas span {
    background-color: $cyan;
}