@use 'base/variables' as *;
@use 'base/mixins' as *;

.login {
    // padding-top: 5rem;
    margin-top: 5rem;
    margin-bottom: 5rem;
    .uptask {
        @include textoDegradado($cyan);
    }

    input[type="submit"]{
        @include boton($cyan, $blanco);
    }

    .acciones {
        a {
            color: $cyan;
            &:hover {
                color: darken($cyan, 10%);
            }
        }
    }
}

