@use 'base/variables' as *;
@use 'base/mixins' as *;

.barra {
    background: $blanco;
    padding: 1rem;
    width: 100%;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, .4);
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
    z-index: 1;
    position: fixed;

    @include telefono {
        padding: 1rem 2rem;
    }

    @include Pantallas(300px){
        padding: 1rem .5rem;
        gap: .5rem;
    }

    @include Pantalla(800px) {
        p {
            display: block !important;
        }
    }

    h2 {
        font-size: 2.5rem;
        text-transform: capitalize;
        @include Pantallas (480px) {
            font-size: 2rem;
        }

        @include Pantallas(300px){
            letter-spacing: -1px;
        }
    }

    h2,
    p {
        color: $indigo;
        margin: 0;
    }

    p {
        font-size: 1.8rem;
        display: none;
    }

    .contenedor-flex {
        display: flex;
        align-items: center;
        gap: .5rem;

        @include telefono {
            gap: 1rem;
        }

        .contenedor-svg {
            &:hover {
                svg {
                    transform: rotate(90deg);
                    transition-property: transform;
                    transition-duration: .2s;
                }

            }
        }

        .contenedor-svg,
        .contenedor-svg-info {
            width: 4rem;
            height: 4rem;
            padding: .5rem;
            border-radius: .3rem;
            @include Pantallas (480px) {
                width: 3.5rem;
                height: 3.5rem;
                padding: .25rem;
            }

            &:hover {
                background: lighten(#42526E, 60%);

                .path1,
                .path2,
                .rect1 {
                    animation: salto .5s ease infinite;
                    -webkit-animation: salto .5s ease infinite;
                }

                .path3,
                .path4,
                .rect2 {
                    animation: salto2 .5s ease infinite;
                    -webkit-animation: salto2 .5s ease infinite;
                }
            }
        }

    }

    .contenedor-svg-info__equipo {
        &:hover {
            svg {
                animation: salto3 .5s ease infinite;
                -webkit-animation: salto3 .5s ease infinite;
            }
        }
    }

    .contenedor-flex__background {
        padding: .6rem;
        border-radius: .3rem;

        img {
            width: 2.5rem;
            height: 2.5rem;
            @include Pantallas (480px) {
                width: 2rem;
                height: 2rem;
            }
        }

        @include Pantallas(300px){
            padding: 0;
        }

        &:hover {
            background: lighten(#42526E, 60%);
            cursor: pointer;
        }
    }

    .nombre-usuario {
        padding: .6rem;
        border-radius: .3rem;
        font-weight: 400;

        &:hover {
            background: lighten(#42526E, 60%);
            cursor: pointer;
        }
    }

    .contenedor-svg-info__giro {
        svg:hover {
            transform: rotate(360deg);
            transition-property: transform;
            transition-duration: .3s;
        }
    }

    @-webkit-keyframes salto {
        0% {
            transform: translateX(0);
        }

        50% {
            transform: translateX(-2px);
        }

        100% {
            transform: translateX(0.5px);
        }
    }

    @keyframes salto {
        0% {
            transform: translateX(0);
        }

        50% {
            transform: translateX(-2px);
        }

        100% {
            transform: translateX(0.5px);
        }
    }

    @-webkit-keyframes salto2 {
        0% {
            transform: translateX(0.5px);
        }

        50% {
            transform: translateX(2px);
        }

        100% {
            transform: translateX(0);
        }
    }

    @keyframes salto2 {
        0% {
            transform: translateX(0.5px);
        }

        50% {
            transform: translateX(2px);
        }

        100% {
            transform: translateX(0);
        }
    }

    @-webkit-keyframes salto3 {
        0% {
            transform: translateY(0.5px);
        }

        50% {
            transform: translateY(2px);
        }

        100% {
            transform: translateY(0);
        }
    }

    @keyframes salto3 {
        0% {
            transform: translateY(0.5px);
        }

        50% {
            transform: translateY(2px);
        }

        100% {
            transform: translateY(0);
        }
    }

    .contenedor-svg,
    .perfil-dashboard,
    .contenedor-svg-info {
        cursor: pointer;
    }
}

#nueva-tarea{
    @include Pantallas(300px){
        display: none;
    }
}