@use 'base/variables' as *;
@use 'base/mixins' as *;

.modal {
    position: absolute;
    background-color: rgba(0, 0, 0, .8);
    backdrop-filter: blur(2px);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3;
    padding: 0 1rem;

    .formulario {
        max-width: 60rem;
        background-color: $blanco;
        padding: 2rem;
        margin: 0 auto;
        border-radius: .5rem;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: .2s;
        transition-timing-function: ease-out;
        opacity: 0;

        legend {
            text-align: center;
            font-size: 2rem;
            font-weight: bold;
            margin-bottom: 2rem;
            display: flex;
            gap: 2rem;
            justify-content: center;
            text-transform: capitalize;
            flex-direction: column;
        }

        input {
            &::placeholder {
                color: $gris;
            }
        }

        input[type="text"] {
            background-color: lighten($gris, 40%);

            &:focus,
            &:not(:placeholder-shown) {
                border: 1px solid $negro;
                background-color: transparent;
            }
        }

        .opciones {
            display: flex;
            justify-content: space-between;
            margin-top: 3rem;

            input,
            button {
                border-radius: .25em;
            }
        }

        .submit-nueva-tarea {
            @include boton($indigo, $blanco);
            display: inline-block;
            margin: 0;
            border: 1px solid $indigo;
            padding: 1.5rem 2rem !important;
            -webkit-box-shadow: 0px 0px 2px 3px rgba(255, 255, 255);
            box-shadow: 0px 0px 2px 3px rgba(255, 255, 255);

            &:hover {
                margin-left: -1px;
                background-color: $indigo;
                border: 1px solid $grisClaro;
                -webkit-box-shadow: 0px 0px 2px 3px rgba(66, 56, 200, 0.71);
                box-shadow: 0px 0px 2px 3px rgba(66, 56, 200, 0.71);
            }
        }

        .cerrar-modal {
            @include boton($naranja, $blanco);
            display: inline-block;
            margin: 0;
            border: 1px solid $naranja;

            &:hover {
                background-color: $naranja;
                border: 1px solid $grisClaro;
                -webkit-box-shadow: 0px 0px 2px 3px rgba(245, 158, 11, 0.71);
                box-shadow: 0px 0px 2px 3px rgba(245, 158, 11, 0.71);
            }
        }

        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
            transition-property: all;
            transition-duration: .2s;
        }

        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
            transition-property: all;
            transition-duration: .2s;
        }
    }
}