@use 'base/variables' as *;
@use 'base/mixins' as *;

.confirmar {
    margin-top: 5rem;
    .uptask {
        @include textoDegradado($indigo);
    }

    .acciones {
        justify-content: center;
        a {
            color: $indigo;
            &:hover {
                color: darken($indigo, 10%);
            }
        }
    }
}