@use 'base/variables' as *;
@use 'base/mixins' as *;

.alerta {
    padding: 1rem 1rem 1rem 2rem;
    text-transform: uppercase;
    font-weight: 900;
    margin-bottom: 1rem;
    font-size: 1.4rem;
    max-width: 80rem;

    &.error {
        background-color: lighten($rojo, 60%);
        border-left: .5rem solid $rojo;
        color: $rojo;
        border-radius: 3px;
    }

    &.exito {
        background-color: lighten($verde, 60%);
        border-left: .5rem solid $verde;
        color: $verde;
        border-radius: 3px;
    }
}

.oculto {
    display: none;
    visibility: hidden;
}

//----------------------------------------------------
.contenedor-paginaerror {
    padding: 2rem 0;
    background: #3AA3FF;
    overflow: hidden;
    width: 100%;
    height: 100vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    .mensaje-paginaerror {
        flex-basis: 40%;
        background-color: $blanco;
        padding: 1rem 2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: flex-start;

        @include Pantallas($tablet) {
            padding: 4rem 2rem 6rem 2rem;
            flex-basis: 100%;
        }

        h1 {
            font-weight: bold;
            font-size: 10rem;
            margin-bottom: 0;
            line-height: 1;
        }

        h3 {
            font-size: 2.5rem;
            line-height: 2.5rem;
        }

        h1,
        h3 {
            text-align: start;
        }

        p {
            line-height: 2.3rem;
            margin: 1rem 0 4rem 0;
        }

        a {
            background-color: $negro;
            color: $blanco;
            cursor: pointer;
            border: none;
            padding: 1rem;
            width: 50%;
            text-transform: uppercase;
            border: 1px solid $negro;
            text-align: center;
            line-height: 1.4rem;
            font-size: 1.6rem;

            @include Pantallas($telefono) {
                width: 100%;
            }

            &:hover {
                transition: all 0.3s;
                background-color: transparent;
                color: $negro;
            }
        }
    }

    .imagen-paginaerror {
        flex-basis: 60%;

        @include Pantallas($tablet) {
            display: none;
            visibility: hidden;
        }
    }

    .seccion {
        padding-top: 3rem;
        padding-bottom: 3rem;
    }

    .flexbox {
        display: flex;
        gap: 2rem;
    
        @include Pantallas($telefono) {
            flex-direction: column;
            gap: 0;
        }
    }
}


