@use 'base/variables' as *;
@use 'base/mixins' as *;

.formulario,
.form {
    margin-top: 3rem;

    .campo {
        position: relative;
        width: 100%;
        margin-bottom: 1.5rem;

        input {
            display: block;
            width: 100%;
            border: 1px solid transparent;
            border-radius: 3px;
            padding: 1rem;
            background-color: lighten($gris, 40%);
            color: $negro;
            outline: none;

            &::placeholder {
                color: transparent;
            }

            &:focus+.label,
            &:not(:placeholder-shown)+.label {
                top: -10px;
                z-index: 1;
                color: $negro;
                font-size: 1.5rem;
                background-color: $blanco;
            }

            &:focus,
            &:not(:placeholder-shown) {
                border: 1px solid $negro;
                background-color: transparent;
            }
        }

        .label {
            position: absolute;
            top: 25%;
            left: 1rem;
            color: $negro;
            transition: 0.5s;
            z-index: 1;
            margin: 0;
        }
    }

    .contenedor-ver:last-of-type{
        margin-bottom: 3rem;
    }

    input[type="submit"] {
        margin: 0 auto;
    }
}


.contenedor-ver {
    display: flex;
    position: relative;
    margin: 0;

    .check {
        margin: 0;
        position: absolute;
        right: 0;
        z-index: 1;
        top: 15%;

        img {
            width: 2.5rem;
            height: 2.5rem;
            margin-right: .5rem;
            filter: invert(1);
        }
    }
}

.form,
.editar-perfil {
    .campo {
        input {

            &:focus+.label,
            &:not(:placeholder-shown)+.label {
                top: -10px;
                z-index: 1;
                color: $negro;
                font-size: 1.5rem;
                background: linear-gradient(to bottom, $grisClaro 0%, $grisClaro 50%, $blanco 50%, $blanco 100%);
            }
        }
    }
}

.editar-perfil .campo {
    input {

        &:focus,
        &:not(:placeholder-shown) {
            border: 1px solid transparent;
        }
    }
}

.dashboard .formulario input:not([type=submit]) {
    &:disabled {
        color: $gris;
        cursor: no-drop;
    }
}

.consejo-password{
    margin: -4rem 0 3rem 0;
    font-size: 1.5rem;
    span{
        font-size: 1.7rem;
        color: $rojo;
    }
}

.espaciado {
    letter-spacing: .3rem;
}