@use 'base/variables' as *;
@use 'base/mixins' as *;

.crear {
    margin-top: 5rem;
    padding-bottom: 5rem !important;

    .uptask {
        @include textoDegradado($morado);
    }

    input[type="submit"] {
        @include boton($morado, $blanco);
    }

    .acciones {
        a {
            color: $morado;

            &:hover {
                color: darken($morado, 10%);
            }
        }
    }
}

.switch-field {
    overflow: hidden;
}

.switch-field input {
    width: 0px;
}

.switch-field label {
    color: $negro;
    padding: 1rem 2rem;
    text-align: center;
    transition: all .2s;
    margin-right: -1px;
    cursor: pointer;
}

#fichero, .fichero {
    border-radius: 100%;
    box-shadow: 0 0 0.5px 1px #4338ca;
    z-index: 0;
}

.fichero {
    width: 55px;
    height: 55px;
}

#fichero {
    margin: 0 0 0 -1rem;
    width: 70px;
    height: 70px;
}

.flex,
.switch-field {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    padding: 1rem 0;
    align-items: flex-end;
    flex-wrap: wrap;
}

.perfil {
    background-repeat: no-repeat;
    background-size: cover;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    border: 1px solid #D8DADF;
    margin: 0 0 1.5rem -.5rem;

    &:hover {
        transform: scale(1.1);
        z-index: 1;
    }

    &:nth-child(2) {
        background-image: url(../img/perfil1.webp);
    }

    &:nth-child(4) {
        background-image: url(../img/perfil2.webp);
    }

    &:nth-child(6) {
        background-image: url(../img/perfil3.webp);
    }

    &:nth-child(8) {
        background-image: url(../img/perfil4.webp);
    }

    &:nth-child(10) {
        background-image: url(../img/perfil5.webp);
    }

    &:nth-child(12) {
        background-image: url(../img/perfil6.webp);
    }

    &:nth-child(14) {
        background-image: url(../img/perfil7.webp);
    }

    &:nth-child(16) {
        background-image: url(../img/perfil8.webp);
    }
}

.ps {
    margin: 0 !important;
}

.d {
    font-weight: normal;
}