@use 'variables' as *;

/** Media Queries **/
@mixin telefono {
    @media (min-width: $telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: $tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: $desktop) {
        @content;
    }
}

@mixin Pantalla ($mediaQuerySize) {
    @media (min-width: $mediaQuerySize) {
        @content;
    }
}

@mixin Pantallas ($mediaQuerySize) {
    @media (max-width: $mediaQuerySize) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.6rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    cursor: pointer;

    // width: 100%;
    @include tablet {
        width: auto;
    }

    &:hover {
        background-color: darken($bgColor, 10%);
    }
}

@mixin boton2($bgColor, $colorTexto, $bgColorH) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.6rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    cursor: pointer;
    width: 100%;

    &:hover {
        background-color: darken($bgColorH, 10%);
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin textoDegradado ($color) {
    text-align: center;
    background: linear-gradient(to right, $color 0%, lighten($color, 30%) 100%, );
    color: transparent;
    background-clip: text;
    font-size: 7rem;

    @include tablet {
        font-size: 10rem;
    }
}

@mixin hover($bgColor) {
    border: 1px solid $bgColor;
    background-color: $blanco;
    color: $bgColor;

    &:hover {
        background-color: darken($bgColor, 5%);
        color: $blanco;

        p::after {
            color: $blanco;

        }
    }
}