@use 'base/variables' as *;
@use 'base/mixins' as *;

.recuperar {
    margin-top: 5rem;
    .uptask {
        @include textoDegradado($rosa);
    }

    input[type="submit"]{
        @include boton($rosa, $blanco);
    }

    .acciones {
        a {
            color: $rosa;
            &:hover {
                color: darken($rosa, 10%);
            }
        }
    }
}