@use 'base/variables' as *;
@use 'base/mixins' as *;

.reestablecer {
    margin-top: 5rem;
    .uptask {
        @include textoDegradado($azul);
    }

    input[type="submit"]{
        @include boton($azul, $blanco);
    }

    .acciones {
        a {
            color: $azul;
            &:hover {
                color: darken($azul, 10%);
            }
        }
    }
}