@use 'base/variables' as *;

@media (prefers-color-scheme: dark) {
    $grisOscuro: #2c2c3b;

    body,
    .dashboard {
        background-color: $grisOscuro;
    }

    .tagline,
    .contenedor-sm .acciones a,
    .barra h2,
    .barra p,
    .contenedor-flex__background h2,
    .dashboard .cuenta h2,
    .dashboard .informacion-cuenta h3,
    .dashboard .informacion-cuenta p,
    .dashboard .menu-cuenta li a,
    .cerrar-session,
    .editar-perfil .campo .label,
    .form .campo .label,
    .formulario .campo .label,
    .tarea p,
    .form .campo input, .formulario .campo input{
        color: $blanco;
    }

    .login,
    .crear,
    .recuperar {
        .formulario .campo .label {
            color: $negro;
        }
    }

    .dashboard .formulario input:not([type=submit]) {
        background-color: transparent;
        border: 1px solid $blanco;
        color: $blanco;
    }

    .descripcion-pagina,
    .consejo-password,
    .filtros h2 {
        color: $grisClaro;
    }

    .dashboard .informacion-cuenta img,
    #fichero,
    .fichero {
        box-shadow: 0px 0px .5px 1px rgba(243, 244, 246, 0.60);
    }


    .campo input {
        background-color: lighten($gris, 40%);
        color: $blanco;
        transition-duration: .2s;
        transition-property: color;

        &:focus+.label,
        &:not(:placeholder-shown)+.label {
            background-color: $grisOscuro  !important;
            color: $blanco  !important;
        }

        &:focus,
        &:not(:placeholder-shown) {
            border: 1px solid $blanco  !important;
        }
    }


    .form,
    .editar-perfil {
        .campo input {

            &:focus+.label,
            &:not(:placeholder-shown)+.label {
                background: $grisOscuro;
            }
        }
    }

    
    .sidebar .flecha.fixed {
        filter: invert(0);
    }

    .contenedor-ver .campo:hover + .check img{
        filter: invert(0);
    }

    .alerta.error,
    .contenedor-flex__background img {
        background-color: $blanco;
    }

    .barra {
        box-shadow: 1px 12px 9px -13px rgba(255, 255, 255, .1);
    }

    .nombre-pagina,
    .dashboard .editar-perfil .campo input:disabled {
        color: darken($grisClaro, 15%);
    }

    .contenedor-flex__background img {
        border-radius: .4rem;
    }

    .barra,
    .dashboard .cuenta,
    .sidebar {
        background-color: darken($grisOscuro, 5%);
    }

    .barra .contenedor-flex__background:hover,
    .barra .nombre-usuario:hover,
    .barra .contenedor-flex .contenedor-svg:hover,
    .barra .contenedor-flex .contenedor-svg-info:hover,
    .sidebar .flecha.fixed:hover,
    .sidebar .flecha:hover,
    .dashboard .menu-cuenta li a:hover,
    .cerrar-session:hover,
    .sidebar .sidebar-nav a:hover,
    .sidebar .sidebar-nav a.actual,
    .no-style:hover {
        background-color: darken($grisClaro, 60%);
    }

    .switch .toggle,
    .switch input[type=checkbox]:checked~.toggle,
    .tarea .opciones .eliminar-tarea {
        background-color: darken($blanco, 10%);
    }

    .dashboard .perfil-dashboard:hover {
        box-shadow: 0px 0px 2px 2px rgba(243, 244, 246, 0.60);
    }

    .contenedor-svg svg,
    .contenedor-svg-info svg {
        stroke: $blanco;
    }

    .dashboard .informacion-cuenta {
        border-top: 1px solid $grisClaro;
    }

    .dashboard .informacion-cuenta,
    .dashboard .menu-cuenta {
        border-bottom: 1px solid $grisClaro;
    }

    .tarea:hover {
        box-shadow: 0 8px 16px -4px rgb(0 0 0 / 25%), 0 0 0 1px rgb(0 0 0 / 8%);
    }

    .tarea {
        border-bottom: 2px solid darken($grisClaro, 60%);
    }

    .filtros {
        background-color: lighten($grisOscuro, 10%);
    }

    .listado-tareas .tarea:hover {
        background-color: lighten($grisOscuro, 10%);
    }

    .modal .formulario {
        background-color: $grisOscuro;

        legend {
            color: $grisClaro;
        }
    }

    .modal .formulario .submit-nueva-tarea {
        box-shadow: 0 0 2px 3px $grisOscuro;
    }

    .filtros input[type=radio]:checked~label,
    .filtros input[type=radio]:hover~label {
        background-color: $grisOscuro;
    }
}