@use 'base/variables' as *;
@use 'base/mixins' as *;

.listado-proyectos {
    list-style: none;
    margin-top: 3rem;
    display: flex;
    gap: 2rem;
    width: 100%;
    padding: 0;
    // grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

    .proyecto {
        background-image: url(../img/fondo5.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        min-height: 10rem;
        width: 250px;
        display: flex;
        align-items: center;
        // max-width: 100%;
        border-radius: .5rem;
        position: relative;

        @include Pantallas(600px){
            width: 100%;
        }

        &::after {
            content: "";
            position: absolute;
            border-radius: .5rem;
            background: rgba(0, 0, 0, .3);
            backdrop-filter: blur(.5px);
            width: 100%;
            min-height: 10rem;
            margin: 0;
            display: inline-block;
            top: 0;
            bottom: 0;
            left: 0;
            z-index: 0;
        }

        &:hover{
            &::after {
                background-color: rgba(0, 0, 0, .6);
                transition-property: background-color;
                transition-duration: .2s;
            }
        }

        a {
            color: $blanco;
            font-weight: 500;
            letter-spacing: -.5px;
            text-transform: capitalize;
            z-index: 1;
            position: absolute;
            display: block;
            width: 100%;
            height: 100%;
            padding: 2rem;
        }
    }
}
