@use 'base/variables' as *;
@use 'base/mixins' as *;

.sidebar {
    background-color: $indigo;
    padding: 2rem;
    width: 26rem;
    max-width: 26rem;
    height: 91vh; //100
    position: absolute;
    left: 0;
    transition-property: left;
    transition-duration: .3s;
    z-index: 2;

    h2 {
        color: $blanco;
        margin: 0;
        text-align: center;
    }

    .sidebar-nav {
        margin-top: 2rem;

        a {
            color: $blanco;
            display: block;
            margin-bottom: 1.5rem;
            text-transform: capitalize;
            font-weight: 400;
            // font-size: 1.8rem;
            padding: .7rem;
        }
    }

    .sidebar-nav a,
    .flecha {
        border-radius: .5rem;
        transition-property: background-color;
        transition-duration: .3s;

        &:hover, &.actual {
            background-color: darken($indigo, 20%);
        }
    }

    .flecha {
        position: absolute;
        top: 0;
        right: 0;
        width: 3rem;
        height: 3rem;
        margin: .5rem .5rem 0 0;
        z-index: 1;
        cursor: pointer;
        transform: rotate(180deg);

        &.fixed{
            transform: rotate(0deg); 
            right: -4rem;
            filter: invert(1);
            &:hover {
                background-color: darken(#bdc737, 20%);
            }
        }
    }
}

.ocultar {
    left: -26rem;
    transition-property: left;
    transition-duration: .3s;
}