@use 'variables' as *;
@use 'base/mixins' as *;

*,
*:before,
*:after {
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}

body {
    font-family: $fuente_textos;
    font-size: 1.8rem;
    // background-image: url(../img/blob-scene-haikei.svg);
    // background-repeat: no-repeat;
    // background-size: contain;
    // height: 100%;
    // backdrop-filter: blur(5px);
    // background-position: center center;
    // width: 100%;
    // min-height: 100%;
    // overflow: auto;
    // @include tablet {
    //     overflow: hidden;
    // }

    &::-webkit-scrollbar{
        width: .5rem;
    }

    &::-webkit-scrollbar-track {
        background: #fff;
        border-radius: 5rem;
    }

    &::-webkit-scrollbar-thumb {
        background: lighten($indigo, 20%);
        border-radius: 5rem;
        height: 10rem;
    }

    ::-webkit-scrollbar{
        width: .5rem;
    }

    ::-webkit-scrollbar-track {
        background: #fff;
        border-radius: 5rem;
    }

    ::-webkit-scrollbar-thumb {
        background: lighten($indigo, 20%);
        border-radius: 5rem;
        height: 10rem;
    }
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

.contenedor-sm {
    max-width: 70rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

.swal2-popup {
    width: 40rem !important;
    font-size: 1.8rem !important;
}