@use 'base/variables' as *;
@use 'base/mixins' as *;

.contenedor-nueva-tarea {
    display: flex;
    justify-content: center;
    position: fixed;
    bottom: 2%;
    right: 2%;
    z-index: 3;

    @include Pantalla (950px){
        bottom: 3%;
        right: 6%;
    }

    .agregar-tarea {
        @include boton($indigo, $blanco);
        font-size: 3.6rem;
        font-weight: normal;
        padding: .5rem;
        width: 5rem;
        text-align: center;
        height: 5rem;
        text-align: center;
        border-radius: 50%;

        @include tablet {
            width: 5rem;
        }
    }
}

.listado-tareas {
    list-style: none;
    padding: 0;
    margin-top: 3rem;

    .no-tareas {
        text-align: center;
    }
}

.tarea {
    padding: .5rem 2rem;
    border-bottom: 2px solid darken($grisClaro, 5%);
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;

    @include Pantallas(370px){
        flex-direction: column;
        gap: 0;
        padding: .5rem 2rem 1rem 2rem;
        justify-content: center;
        align-items: center;
    }

    &:last-of-type {
        border: none;
    }

    &:hover {
        background: lighten($indigo, 40%);
        border-radius: .3rem;
        box-shadow: 0 8px 16px -4px rgb(255 255 255 / 25%), 0 0 0 1px rgb(255 255 255 / 8%);
    }

    p {
        font-size: 1.8rem;
        color: $negro;
        flex: 50%;

        &::first-letter {
            text-transform: uppercase;
        }

        @include Pantallas(370px){
            text-align: center;
        }
    }

    .opciones {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        gap: 1rem;

        @include Pantallas(370px){
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }

        button {
            border: none;
            font-size: 1.5rem;
            padding: .5rem 1rem;
            text-transform: capitalize;
            font-weight: 500;
            border-radius: 3rem;
            color: $blanco;
            text-align: center;
            width: 13rem;
            height: 2.9rem;
            max-width: 100%;
            cursor: pointer;
        }

        .pendiente {
            @include hover($naranja);
        }

        .completa {
            @include hover($verde);
        }

        .eliminar-tarea {
            @include hover($rojo2);
        }
    }

    .Completa {
        background-color: $verde;
    }
    
    .Pendiente {
        background-color: $naranja;
    }
    
    .Completa, .Pendiente {
        padding: .5rem 2rem;
        margin: 0;
        border-radius: 5rem;
        color: $blanco;
    }
}

.switch {
    input[type="checkbox"] {
        display: none;
        cursor: pointer;
    }

    input[type="checkbox"]:checked~.toggle {
        @include hover($verde);

        p::after {
            content: "Completa";
            color: $verde;
            justify-content: flex-start;
            padding-left: 1.5rem;
            transition-property: justify-content;
            transition-duration: .3s;
        }
    }

    input[type="checkbox"]:checked~.toggle label {
        left: 10.06rem;
        bottom: 0;
        top: 0;
        height: 2.55rem;
        width: 2.55rem;
        margin: auto 0;
        transition: all .3s;
        transform: rotate(360deg);
        background: $verde;

        &::after {
            content: url(../img/check.svg);
            padding: .1rem;
            transition: all .3s;
        }
    }

    .toggle,
    label {
        border-radius: 5rem;
    }

    .toggle {
        height: 3rem;
        width: 13rem;
        @include hover($naranja);
        position: relative;

        p {
            margin: 0;
            width: 100%;
            height: 100%;

            &::after {
                content: "Pendiente";
                color: $naranja;
                width: 100%;
                height: 100%;
                font-weight: 500;
                display: flex;
                padding-right: 1.5rem;
                font-size: 1.5rem;
                justify-content: flex-end;
                align-items: center;
                transition-property: justify-content;
                transition-duration: .3s;
            }

        }
    }

    label {
        height: 2.5rem;
        width: 2.5rem;
        background: $naranja;
        position: absolute;
        left: 0;
        margin: auto 0;
        margin-left: .2rem;
        top: 0;
        bottom: 0;
        cursor: pointer;
        transition: all .3s;

        &::after {
            content: url(../img/close.svg);
            height: 2.6rem;
            width: 2.6rem;
            position: absolute;
            transition: all .3s;
        }
    }
}

.margin-bottom {
    margin-bottom: 4rem;
}

