@use 'base/variables' as *;
@use 'base/mixins' as *;

.dashboard {
    background-color: $grisClaro;
    height: 100vh;
    overflow-y: scroll;

    .perfil-dashboard {
        background-size: contain;
        width: 45px;
        height: 45px;
        border-radius: 100%;
        margin: 0;

        &:hover {
            // box-shadow: 0 0 0 .2rem $indigo;
            box-shadow: 0px 0px 2px 2px rgba(66, 56, 200, 0.71);
            transition-property: box-shadow;
            transition-duration: .1s;
        }
    }

    .cuenta {
        background-color: $blanco;
        position: absolute;
        top: 7rem;
        right: 1rem;
        border-radius: .3rem;
        padding: 1.2rem;
        min-width: 31rem;
        max-width: 38rem;
        word-break: break-word;
        box-shadow: 0 8px 16px -4px rgb(9 30 66 / 25%), 0 0 0 1px rgb(9 30 66 / 8%);
        overflow: hidden;
        z-index: 3;

        @include Pantallas (300px) {
            min-width: 26.5rem;
        }

        h2 {
            text-align: center;
            font-size: 1.8rem;
            color: $gris;
            font-weight: 500;
            margin-bottom: 1rem;
        }
    }

    .informacion-cuenta {
        display: flex;
        // grid-template-columns: minmax(auto, 1fr);
        // grid-template-rows: repeat(1, 1fr);
        gap: 1rem;
        border-top: 1px solid rgba(9, 30, 66, 0.13);
        border-bottom: 1px solid rgba(9, 30, 66, 0.13);
        padding: 1.5rem 0;
        flex-wrap: wrap;

        div {
            // grid-area: 1 / 2 / 1 / 2;
            margin: auto 0;
        }

        h3,
        p {
            margin: 0;
            font-size: 1.3rem;
            font-weight: 500;
        }
    }

    .menu-cuenta {
        margin: 0;
        padding: 1.5rem 0;
        border-bottom: 1px solid rgba(9, 30, 66, 0.13);

        li {
            list-style: none;
            margin-bottom: .5rem;
            width: 100%;

            &:last-of-type {
                margin-bottom: 0;
            }

            a {
                display: block;
                font-size: 1.5rem;
                font-weight: 400;
                color: $negro;
                border-radius: .5rem;
                padding: .5rem .3rem;
                transition-property: background-color;
                transition-duration: .2s;

                &:hover {
                    background-color: darken($blanco, 20%);
                }
            }
        }
    }

    .contenido {
        width: 100%;
        padding: 2rem;

        .nombre-pagina {
            font-size: 2.5rem;
            text-align: center;
            text-transform: capitalize;
            margin-top: 2rem;
            position: relative;

            span {
                font-size: 1rem;
                position: absolute;
                top: -24%;
                color: $gris;
                font-family: $fuente_textos;
            }
        }
    }

    .formulario {
        input[type="submit"] {
            @include boton($indigo, $blanco);
        }

        input:not([type="submit"]) {
            background-color: $blanco;
        }
    }

    .principal {
        margin-top: 6.5rem;
    }
}

.no-style,
.cerrar-session {
    @include boton2(transparent, $blanco, $indigo);
    margin-bottom: 1.5rem;
    padding: .7rem;
    border-radius: .5rem;
    text-transform: capitalize;
    font-weight: 400;
    font-size: 1.8rem;
    text-align: start;
    width: 100%;
}


.cerrar-session {
    font-size: 1.5rem;
    font-weight: 400;
    color: $negro;
    padding: .5rem .3rem;
    text-transform: capitalize;
    margin-top: 1.5rem;

    &:hover {
        background-color: darken($blanco, 20%);
    }
}

.no-visible {
    visibility: hidden;
}

.grupo {
    width: 2.5rem;
    height: 2.5rem;
    transform: translate(868%, 136%);
    z-index: 1;

    svg {
        stroke: $blanco;
    }
}